@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600&display=swap');

:root{
    --mainColor: #fff;
    --mainLightColor: rgba(235,235,235,0.5);
    --mainDimColor: rgb(228,228,228);
}

body{
    margin: 0;
    padding: 0;
    overflow: hidden;
    box-sizing: border-box;
}

.container{
    min-height: 100vh;
    min-width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--mainColor);
    font-family: 'Poppins';   
}

.backgroundVideo{
    position: absolute;
    right: 0;
    top: 0;
    width: 100%;
    object-fit: cover;
    height: 100vh;
    z-index: -1;
    filter: saturate(2.5);
}

.blackScreen{
    width: 100vw;
    height: 100vh;
    position: absolute;
    pointer-events: none;
    background-color: rgba(17,17,17,0.2);
}

.music-Container{
    width: 350px;
    padding: 35px 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-radius: 36px;
    box-shadow: 0 0 20px rgba(26,26,26,0.1),
    0 0 40px rgba(26,26,26,0.1),
    0 0 80px rgba(26,26,26,0.1);
    backdrop-filter: blur(15px);
    font-weight: 600;
}

/* Media query for mobile screens */
@media (max-width: 767px) {
    .music-Container {
        margin-top: -30px; /* Adjust this value as needed */
    }
}

.musicPlayer{
    margin: 0;
    margin-bottom: 10px;
    color: var(--mainLightColor);
}

.music-Head-Name{
    margin: 0 auto;
    font-size: 1.5rem;
    text-align: center;
}

.music-Artist-Name{
    color: var(--mainDimColor);
    margin: 4px 0;
    font-size: 1.2rem;
    font-weight: 400;
}


#songAvatar{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    margin: 18px 0;
    position: relative;
    cursor: pointer;
    animation: animateAvatar .5s linear infinite;
}

@keyframes animateAvatar {
    0%,100%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.03);
    }
}

.musicTimerDiv{
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-weight: 600;
}

.musicProgressBar{
    width: 100%;
    margin-bottom: 16px;
    height: 10px;
    border-radius: 5px;
    outline: none;
    filter: hue-rotate(20deg);
}

.playBtn{
    font-size: 32px;
    margin: 8px 10px;
    color: var(--mainColor);
    cursor: pointer;
    font-weight: 400;
}

.musicControler{
    cursor: pointer;
    font-size: 28px;
    color: var(--mainColor);
}

.changeBackBtn{
    width: 200px;
    height: 15px;
    background-color: #eee;
    position: absolute;
    border-radius: 12px 12px 0 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;
    transition: .3s;
    color: #eee;
    cursor: pointer;
    padding: 3px 0 0 0;
    font-weight: 600;
    text-align: center;
}

.changeBackBtn:hover{
    height: 30px;
    color: #222;
}

@media (max-width: 767px) {
    .changeBackBtn {
        bottom: auto; /* Reset bottom position */
        top: 0; /* Position at the top */
        transform: translateX(-50%) rotate(180deg); /* Rotate button 180 degrees */
    }

    .changeBackBtn span {
        display: inline-block;
        transform: rotate(180deg); /* Rotate text within the button */
    }


}

.objectFitCover{
    object-fit: cover;
}

.objectFitContain{
    object-fit: contain;
}

/* Subs */

.youtube-Subs{
    position: absolute;
    bottom: 15px;
    left: 15px;
    text-decoration: none;
    color: #fff;
    display: flex;
    align-items: center;
    cursor: pointer; 
}

.youtube-Subs img{
    height: 50px;
    border-radius: 50%;
}

.youtube-Subs p{
    font-family: 'Poppins';
    margin: 0;
    padding: 0;
    margin-left: 10px;
    font-size: 14px;
}

.confetti {
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: red;
    animation: confetti-fall 1s ease-in-out forwards;
    opacity: 0;
}

@keyframes confetti-fall {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
    }
    100% {
        transform: translateY(500px) rotate(720deg);
        opacity: 0;
    }
}

.word-balloon {
    color: black;
    position: absolute;
    top: -170%; /* Adjust as needed */
    left: 43%; /* Adjust as needed */
    transform: translateX(-50%);
    padding: 10px;
    background-color: white;
    border-radius: 5px;
    border: 1px solid #ccc;
    /* Add more styling as needed */
  }
  

  .word-balloon:after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 10%;
	width: 0;
	height: 0;
	border: 20px solid transparent;
	border-top-color: white;
	border-bottom: 0;
	border-left: 0;
	margin-left: -10px;
	margin-bottom: -20px;
}

  .downloadButton {
    position: absolute;
    top: 35px;
    right: 10px;
    color: #FFFFFF;
    border: none;
    padding: 8px 12px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 26px;
  }
  